import http from '../../common/http.js'
export default {
	data() {
		return {
			collect: 0,
			imgUrl: http.imgUrl,
			shop: [],
			goods: [],
			tabIndex: 1
		}
	},
	onLoad: function(e) {
		console.log(e)

		this.shop_id = e.shop_id
		this.allRequest()
	},
	onShow: function() {

	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {

			this.getDetail()
		},
		//获取店铺详情
		getDetail: function() {
			let _this = this
			let opts = {
				url: 'shop/getDetail',
				method: 'post'
			};
			let param = {
				city_id: '370700',
				user_id: '1629',
				shop_id: _this.shop_id
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				_this.shop = res.data.data.shop
				_this.goods = res.data.data.goods
				_this.collect = res.data.data.shop.user_collect
				console.log(_this.goods)
			}, error => {
				console.log(error);
			})
		},
		//点击商品
		goGoodInfo: function(e) {
			uni.navigateTo({
				url: '../good/bendiGoodsInfo?goods_id=' + e
			})
			// console.log(e)
		},
		//收藏店铺
		collectShop: function() {
			// this.collect = this.collect == 1 ? 0 : 1
			let _this = this
			let opts = {
				url: 'Members/localShopCollect',
				method: 'post'
			};
			let param = {
				city_id: '370700',
				user_id: '1629',
				shop_id: _this.shop_id
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				if (res.data.msg == '收藏成功') {
					_this.collect = 1
				} else if (res.data.msg == '取消收藏成功') {
					_this.collect = 0
				}
				uni.showToast({
					title: res.data.msg
				})
			}, error => {
				console.log(error);
			})

		},
		//拨打电话
		callPhone: function(e) {
			console.log(e)
			uni.showModal({
				title: '提示',
				content: '确定要拨打' + e + '吗',
				success: function(res) {
					if (res.confirm) {
						uni.makePhoneCall({
							// 手机号
							phoneNumber: e,
							// 成功回调
							success: (res) => {

							},
							// 失败回调
							fail: (res) => {
								uni.showToast({
									title: '错误，请稍后再试',
									duration: 2000
								});
							}
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		//tab切换
		clickTabView: function(e) {
			this.tabIndex = e
		}
	}
}
